import React from "react";
import {BasicLayout} from "./layouts/BasicLayout";
import {HomePage} from "./pages/HomePage";

export type RoutesType = {
    [key: string]: {
        element: React.ReactNode;
        path: string;
        index?: boolean;
        children?: RoutesType;
    }
}

export const routes: RoutesType = {
    index: {
        element: <BasicLayout/>,
        path: '/',
        children: {
            home: {
                element: <HomePage/>,
                path: '',
                index: true,
            },
            about: {
                element: <HomePage/>,
                path: 'about',
            },
            blog: {
                element: <HomePage/>,
                path: 'blog',
            },
            dataBank: {
                element: <HomePage/>,
                path: 'data-bank',
            },
        }
    }
}