import React from 'react';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import {routes, RoutesType} from "./Router";
import {defaultTheme} from "@firstbreath/design-system";
import {ThemeContextProvider} from "./context/ThemeContext";

const routesConstructor = (routes: RoutesType) => {
    return Object.values(routes).map((route, index) => {
            if (route.children) {
                return (
                    <Route key={index} element={route.element} path={route.path}>
                        {routesConstructor(route.children).map((child) => child)}
                    </Route>
                )
            } else {
                return <Route index={route.index} key={index} element={route.element} path={route.path}/>
            }
        }
    )
}

function App() {
    return (
        <ThemeContextProvider defaultTheme={defaultTheme}>
            <Router>
                <Routes>
                    {routesConstructor(routes)}
                </Routes>
            </Router>
        </ThemeContextProvider>
    );
}

export default App;
