import React from "react";
import {
    FooterLink,
    FooterLogo,
    FooterMentions,
    FooterNav,
    FooterSocial,
    FooterSocials,
    FooterStyle
} from "./Footer.styles";
import {useTranslation} from "react-i18next";
import {FooterTypes} from "./Footer.types";

export const Footer = ({
                           links,
                           socials
                       }: FooterTypes) => {
    const {t} = useTranslation();

    return (
        <FooterStyle>
            <FooterLogo alt={t("common.alt-logo")}/>
            <FooterNav>
                {links.map((link, index) => (
                    <FooterLink key={index} to={link.url}>{link.name}</FooterLink>
                ))}
            </FooterNav>
            <FooterSocials>
                {socials.map((social, index) => (
                    <FooterSocial
                        key={index}
                        target="_blank"
                        href={social.link}
                        rel="noreferrer"
                    >
                        {social.logo}
                    </FooterSocial>
                ))}
            </FooterSocials>
            <FooterMentions>
                {t("layout.footer.all-right-reserved")}
            </FooterMentions>
        </FooterStyle>
    )
}