import React from 'react';
import {BasicLayoutStyle} from "./BasicLayout.styles";
import {Outlet} from 'react-router-dom';
import {NavBar} from "../../components/NavBar";
import {useTranslation} from 'react-i18next';
import {Footer} from "../../components/Footer";

export const BasicLayout = () => {
    const {t} = useTranslation();
    const links = [
        {name: t("layout.navbar.home"), url: '/'},
        {name: t("layout.navbar.about"), url: '/about'},
        {name: t("layout.navbar.blog"), url: '/blog'},
        {name: t("layout.navbar.data-bank"), url: '/data-bank'}
    ];

    const socials = [
        {
            logo: <img src="/images/socials/facebook.svg" alt={t("common.social-media.facebook")}/>,
            link: 'https://www.facebook.com/'
        },
        {
            logo: <img src="/images/socials/instagram.svg" alt={t("common.social-media.facebook")}/>,
            link: 'https://www.instagram.com/'
        },
        {
            logo: <img src="/images/socials/x.svg" alt={t("common.social-media.facebook")}/>,
            link: 'https://www.twitter.com/'
        },
        {
            logo: <img src="/images/socials/linkedin.svg" alt={t("common.social-media.facebook")}/>,
            link: 'https://www.linkedin.com/'
        }
    ];

    return (
        <BasicLayoutStyle>
            <NavBar links={links}/>
            <Outlet/>
            <Footer links={links} socials={socials}/>
        </BasicLayoutStyle>
    )
}
